@import "../../styles/colors";
@import "../../styles/shadows";
@import "../../styles/transitions";

.chart-card{
  grid-area: chart;
  padding: 32px 24px;
  background-color: $secondary-5;
  border-radius: 6px;
  @include box-shadow(3);

  display: flex;
  flex-direction: column;

  &>*:not(:last-child){
    margin-bottom: 24px;
  }

  .select-group{
    display: flex;
    justify-content: space-between;
    margin-top: auto;

    &>*{
      width: calc(50% - 12px);
    }
  }
}